@use 'variables' as v;

html {
  font-size: 62.5%; // 1 rem = 10 px
  box-sizing: border-box
}

*,
*:before,
*:after {
  box-sizing: inherit;
}

body {
  font-family: v.$main_font;
  font-size: 1.6rem;
  &.fixed-body {
    overflow: hidden;
  }
  &.body-scroll {
    padding-top: 8.6rem;
  }
}

h1,
h2,
h3 {
  margin: 0 0 5rem 0;
}

h1 {
  font-size: 4rem;
}

h2 {
  font-size: 3.6rem;
}

h3 {
  font-size: 3rem;
}

img {
  max-width: 100%;
  width: 100%;
  height: auto;
  display: block;
}

p {
  font-size: 2rem;
  color: v.$black;
  line-height: 1.5;
}

a {
  text-decoration: none;
}

.container {
  width: 95%;
  max-width: 120rem;
  margin: 0 auto
}
