@use 'base/variables' as v;
@use 'base/mixins' as m;

.lineup {
  background-color: v.$pink;
  padding: 5rem 0;
  h3,
  p {
    text-align: center;
    color: v.$white;
  }
  .day {
    text-transform: uppercase;
    margin: 5rem 0;
  }
}

.scene-container {
  @include m.tablet {
    @include m.grid(2,5rem);
  }
  p {
    color: v.$black;
  }
}
.scene {
  padding-top: 2rem;
  .scene-name {
    &:after {
      content: '';
      display: block;
      width: 20rem;
      height: 10rem;
      background-size: 15rem;
      background-repeat: no-repeat;
      background-position: center center;
      margin: 2rem auto 0 auto;
    }
  }
}

.edm .scene-name:after {
  background-image: url(../img/icono_edm.png);
}

.rock .scene-name:after {
  background-image: url(../img/icono_rock.png);
}

.calendar {
  @include m.reset-list;
  li {
    padding: 2.5rem;
    font-weight: v.$bold;
    span {
      font-weight: v.$regular;
    }
  }
}

.bg-yellow {
  background-color: v.$yellow;
  li:nth-child(odd) {
    background-color: darken(v.$yellow, 20%)
  }
}

.bg-green {
  background-color: v.$green;
  li:nth-child(odd) {
    background-color: darken(v.$green, 10%)
  }
}
